.wrapper {
    box-sizing: border-box;
    width: 100vw;
    background-color: #EFEBE6;
    display: flex;
    flex-direction: column;
    padding: 0px 32px;
}

.card{
    width: 100%;
    background-color: transparent;
}
.middleCard{
    width: 100%;
    background-color: transparent;
    margin: 184px 0px;
}


@media (max-width: 1024px) {
    .middleCard{
        margin: 144px 0px;
    }
  
  }
@media (max-width: 768px) {
    .wrapper{
        padding: 0px 20px;
    }
    .middleCard{
        margin: 96px 0px;
    }
  }