.Button {
    box-sizing: border-box;    
    display: flex;
    height: 48px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 14px 0px;    
    border: 2px solid #FFFFFF;
    border-radius: 5px;
    background-color: transparent;
    width: 100%;
    transition: all 1s;
    overflow: hidden;
    
    
}
.Button:hover{
    background-color: #FFFFFF;
   
}
.Button:hover  >  .Text{
    color: #FE3B1F;
}
.Text {

        font-family: 'SEATBCN-Bold';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FFFFFF;
    
    }
