.componentWrapper{
    margin: 0px 32px;
    background-color: #A59C94;
    border-radius: 10px;
    padding: 84px 0px;
    row-gap: 0px;
}

.subWrapper {
    grid-column: auto / span 6;
    position: relative;
}

.cardText{
    
    --ncol: 12;
    --ncolused: 5;
    --gapcol: 32px;
    --firstOperation: calc(var(--ncol) + 1);
    --secondOperation: calc(var(--gapcol) * var(--firstOperation));
    --thirdOperation: calc(100vw - var(--secondOperation));
    --widthOneColumn: calc(var(--thirdOperation) / var(--ncol));
    --widthOneColumnAndGap: calc(var(--widthOneColumn) + var(--gapcol) );
    box-sizing: border-box;
    width: calc(var(--ncolused) * var(--widthOneColumnAndGap));
    padding-left: var(--widthOneColumn);
    

}
 .cardTitle {
    overflow: hidden;
    font-family: 'SEATBCN-Black';
    font-style: normal;
    font-weight: 750;
    font-size: 48px;
    line-height: 52px;
    color: #FFFFFF;    
 }

 .cardSubtitle {


    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;    
    padding-top: 24px;
    overflow: hidden;

 }




 
 .horizontaFirstlLine {
    margin-top: 64px;
    width: 100%;
    
    border-top: 1px solid #CBC4BC;    
}

.horizontalLine {
    width: 100%;
    margin-top: 16px;
    border-top: 1px solid #CBC4BC;    
}

.horizontaFinallLine {
    margin-top: 16px;
    padding-top: 84px;
    border-top: 1px solid #CBC4BC;    
}

.boldText {
    padding-top: 16px;
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    overflow: hidden;

}

.simpleText {

    padding-top: 8px;

    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    overflow: hidden;
}
.imageLeftContainer {
    max-height: 100%;
    box-sizing: border-box;
    width: 100%;
    position: absolute;
     bottom: 0px;
     left: 0px;
     --ncol: 12;
    --gapcol: 32px;
    --firstOperation: calc(var(--ncol) + 1);
    --secondOperation: calc(var(--gapcol) * var(--firstOperation));
    --thirdOperation: calc(100vw - var(--secondOperation));
    --widthOneColumn: calc(var(--thirdOperation) / var(--ncol));
    
    padding-left: var(--widthOneColumn);
    overflow: hidden;
}
.imageContainer{
    max-height: 432px;
    overflow: hidden;
    box-sizing: border-box;
    width: 100%;
    

    margin-bottom: 40px;
}

.imageContainer > img {
    
    object-fit: cover;
}


.buttonWrapper{
    --ncol: 12;
    --ncolused: 2;
    --gapcol: 32px;
    --firstOperation: calc(var(--ncol) + 1);
    --secondOperation: calc(var(--gapcol) * var(--firstOperation));
    --thirdOperation: calc(100vw - var(--secondOperation));
    --widthOneColumn: calc(var(--thirdOperation) / var(--ncol));
    --widthOneColumnAndGap: calc(var(--widthOneColumn) + var(--gapcol) );
    box-sizing: border-box;
    width: calc(var(--ncolused) * var(--widthOneColumnAndGap));
}
.twoButtonsContainer {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
@media (max-width: 1024px) {
    

.cardText{
    --ncol: 6;
 
}
    .buttonWrapper{
        --ncol: 6;
    }
    .componentWrapper{
        row-gap: 56px;
    }
    .imageLeftContainer{
        position: relative;
        padding-left: 0px;
    }

  }

  @media (max-width: 768px) {
  
    .cardTitle {
        
        font-size: 30px;
        line-height: 32px;
        color: #FFFFFF;    
     }
    .componentWrapper{
        padding: 40px 0px;
    }
    .subWrapper {
        grid-column: auto / span 4;
    }
    .cardText{
        padding: 0px 20px;
        width: 100%;
    }
    .buttonWrapper{
        width: 100%;
    }
    .twoButtonsContainer{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
  

  }