
.imageContainer{
    grid-column: auto/ span 8;
    object-fit: cover;
    border-radius: 10px;
}

@media (max-width: 1024px) {

    .imageContainer{
        grid-column: 1 / span 6;
        grid-row: 1 / auto;
    }
  }
@media (max-width: 768px) {

    .imageContainer{
        grid-column: 1 / span 4;
    }
  }