.secondaryContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 9px 23px;
/* Neutral/Grey Dark */

    border: 1px solid #A59C94;
    border-radius: 40px;
    background-color: transparent;

	text-align: center;

    transition: background-color 1s;
    cursor: pointer;
    
}
.secondaryContainer:hover{
    background-color: #000000;
}
.buttonText {
    /* 16/Regular Title */
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'SEATBCN-Regular';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        white-space: nowrap;
    } 
    .secondaryContainer:hover > .buttonText{
    color:#FFFFFF
}