
.wrapper{
  
   position: relative;
   height: fit-content;
    width: 100%;
    background-color: transparent;
}
.emptyDiv{
    width: 100%;
    height: 88px;
    background-color: #000000;

}
.stickyNav{
    width: 100vw;
    overflow-x: scroll;
    height: 80px;
    position: sticky;
    background-color: #FFFFFF;
    z-index: 1;
    top: 0;
    padding: 20px 32px;
    box-sizing: border-box;
    display: flex;
    gap: 16px;
}
.titleWrapper{
    padding: 184px 32px;
    background-color: #EFEBE6;
}
.title{
    overflow: hidden;
    grid-area: 1 / 1 / 1 / span 8;
    font-family: 'SEATBCN-Black';
    font-style: normal;
    font-weight: 750;
    font-size: 60px;
    line-height: 64px;
    color: #000000;
}
.updateText{
    grid-area: 1 / 10 / 1 / span 3;
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #85837F;
    display: flex;
    align-items: center;

}
.accordionWrapper {

        padding: 0px 32px 184px 32px;
        background-color: #EFEBE6;

}
.accordionsubWrapper {

    grid-area: 1 / 1 / 1 / span 8;

}
@media (max-width: 1024px) {

    .titleWrapper{
        padding: 144px 32px;
        row-gap: 64px;
    }  
    .title{
        grid-area: 1 / 1 / 1 / span 6;
        font-size: 48px;
        line-height: 52px;
    }
    .updateText{
        grid-area: 2 / 4 / span 1 / span 3;
    }
    .accordionsubWrapper {

        grid-area: 1 / 1 / 1 / span 6;
    
    }
  }
@media (max-width: 768px) {
    .titleWrapper{
        padding: 96px 20px;
    }
    .title{
        grid-area: 1 / 1 / 1 / span 4;
        font-size: 40px;
        line-height: 44px;
    }
    .updateText{
        grid-area: 2 / 2 / span 1 / span 3;
        font-size: 16px;
        line-height: 24px;
    }
    .accordionWrapper {

        padding: 0px 20px 184px 20px;
    }
    .accordionsubWrapper {

        grid-area: 1 / 1 / 1 / span 4;
    
    }
    
  }