
.wrapper{
  
    height: fit-content;
     width: 100%;
     background-color: transparent;
     position: relative;
 }
 .emptyDiv{
     width: 100%;
     height: 88px;
     background-color: #000000;
 
 }
 .stickyNav{
     width: 100vw;
     overflow-x: scroll;
     height: 80px;
     position: sticky;
     background-color: #FFFFFF;
     z-index: 1;
     top: 0;
     padding: 20px 32px;
     box-sizing: border-box;
     display: flex;
     gap: 16px;
 }



