
.orangeCard {
    grid-column: auto/ span 4;
    background-color: #FE3B1F;
    padding: 32px;
    width: 100%;
    box-sizing: border-box;
    height: fit-content;
    border-radius: 10px;
}




.title{
    font-family: 'SEATBCN-Black';
    font-style: normal;
    font-weight: 750;
    font-size: 40px;
    line-height: 44px;
    color: #FFFFFF;
    margin: 0px;
}
.subtitle {
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    margin: 24px 0px;
}

.text{
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    margin: 0px 0px 56px 0px;
    

}
@media (max-width: 1024px) {
 
  }
@media (max-width: 768px) {
    .title{
        font-size: 30px;
        line-height: 32px;
    }
    
  }