:root {
  --tabletMaxWidth: 1024px;
  --mobileMaxWidth: 768px;

}
.wrapperMainWindow {
    box-sizing: border-box;
    display: grid;
    grid: 1fr / repeat(12, minmax(0, 1fr)); ;
    gap: 32px;
    grid-template-rows: auto;
  
  }
  @media (max-width: 1024px) {
    .wrapperMainWindow {
        grid: 1fr / repeat(6, minmax(0, 1fr)); 
    }
  }
  @media (max-width: 768px) {
    .wrapperMainWindow {
        grid: 1fr / repeat(4, minmax(0, 1fr)); 
        gap: 20px;

    }
  }
 